<template>
  <div class="stock">
    <div class="horizon-content">
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// http://kmandov.github.io/d3-horizon-chart/
const dhc = require('d3-horizon-chart')
import * as d3 from 'd3'
const parseData = (stock, rows) => {
  console.log('parsedata', rows)
  rows = rows.map(function(d) {
      return [d3.timeParse(d.Date), +d.Open];
  }).filter(function(d) {
      return d[1];
  }).reverse();

  var date = rows[0][0],
      compare = rows[400][1],
      value = rows[0][1],
      values = [];

  rows.forEach(function(d, i ) {
      values.push(value = (d[1] - compare) / compare);
  });

  return {
    stock,
    values,
  }
}

export default {
  name: 'Stock',
  components: {
  },
  mounted(){
    // https://bl.ocks.org/kmandov/dcb94f02e71e3560eb3255f2de3120e4
    // https://stackoverflow.com/questions/49534470/d3-js-v5-promise-all-replaced-d3-queue

    let stockLabel = ['AAPL', 'BIDU', 'SINA', 'GOOG', 'MSFT', 'YHOO', 'ADBE', 'REDF', 'INSP', 'IACI', 'AVID', 'CCUR', 'DELL', 'DGII', 'HPQ', 'SGI', 'SMCI', 'SNDK', 'SYNA']

    let promises = stockLabel.map(stock => 
      d3.csv('https://bost.ocks.org/mike/cubism/intro/stocks/' + stock + '.csv')
    )


    Promise.all(promises).then(values => {
      const stocks = values.map((d,i) => parseData(stockLabel[i], d))
      d3.select('.horizon-content').selectAll('.horizon')
          .data(stocks)
          .enter()
          .append('div')
          .attr('class', 'horizon')
          .each(function(d) {
            dhc.horizonChart()
                .height(30)
                .title(d.stock)
                .call(this, d.values);
          });
    })

    //q.awaitAll(function(error, stocks) {
    //    if (error) throw error;

    //    d3.select('.horizon-content').selectAll('.horizon')
    //        .data(stocks)
    //        .enter()
    //        .append('div')
    //        .attr('class', 'horizon')
    //        .each(function(d) {
    //            d3.horizonChart()
    //                .title(d.stock)
    //                .call(this, d.values);
    //        });
    //});

  }
}
</script>

